export default {"name":"南山寿眉茶","img":"\\baicha\\nanshanshoumeicha\\img_20191122101042oratyd.jpg","desc":["南山寿眉茶条索微扁略弯，色泽翠绿披白毫，形似寿者之眉，香气清雅持久，滋味鲜爽醇和，汤色清澈明亮，叶底嫩绿完好。南山寿眉茶的特点这么独特，相信南山寿眉茶的功效也是很不错的。南山寿眉茶的功效相当不错，茶是中国历史悠久的饮品，因其独特的口味、工艺、功效而享誉海内外，被世界公认为全球三大无酒精饮料之一。"],"menus":[{"name":"南山寿眉茶茶叶特点","content":[{"text":"南山寿眉茶条索微扁略弯，色泽翠绿披白毫，形似寿者之眉，香气清雅持久，滋味鲜爽醇和，汤色清澈明亮，叶底嫩绿完好。"},{"src":"\\baicha\\nanshanshoumeicha\\img_20191122101042tvv76o.jpg"}]},{"name":"南山寿眉茶加工工艺","content":[{"text":"南山眉茶的采制技术特点是：嫩采精拣，工艺不繁，技术精湛，连续操作，一气呵成。主要工艺分摊放、杀青、搓条显毫、煇锅四道工序。清明至谷雨前是寿眉茶采摘的黄金季节。采摘标准为一芽一叶和一芽二叶初展，芽长1.5～2.0厘米，通常，炒制500克特级干茶需采6万个左右芽叶。采回的芽叶必须经过精拣细剔，达到嫩、匀、净的目的。拣剔后的芽叶放置在竹匾上摊放2~4小时后再行炒制。"},{"src":"\\baicha\\nanshanshoumeicha\\img_201911221010421oheyo.jpg"},{"text":"杀青：在锅径60厘米的远红外线平锅内进行。锅温120～124℃，投叶量300～400克，采用抖、翻、理手势，将茶叶杀透杀匀。当叶质柔软、色泽转暗即进入搓条显毫工序。"},{"text":"搓条显毫：是寿眉茶成形的关键工序。通过搓条，达到失水搓紧成条、显毫的目的。锅温70～80℃，采用抖、理、搓、抓手法，先理直茶条，再用双手掌合拢搓条。理、搓、抖、抓各种手法灵活掌握，变换进行。用力程度掌握轻、重、轻的原则，即含水量高或低时，动作应轻，避免茶叶结块和断碎。当茶条圆而略扁，茸毫显露，干度达七成干左右，降低锅温至50～60℃，转入煇锅工序。"},{"text":"挥锅：目的是进一步理条整形，达到定形、足干的要求。采用慢速翻炒的手法，促使水分蒸发、理顺茶条，固定形状。炒至茸毫披露，锅温略升10℃左右。当发出持久的清香，捻茶即成粉末，含水量约6%以下时，起锅摊凉贮藏。全程历时35～40分钟。南山寿眉茶的品质特点是，紧圆略扁、匀整，形似扁眉，披毫，色泽翠绿，香高清鲜，滋味醇厚爽口，汤色绿而清澈，叶底嫩匀明亮。"},{"text":"1、鲜叶采摘。"},{"text":"春季开采，采摘标准为玉白色的一芽一叶初展鲜叶。要求匀净、完整、鲜活。"},{"text":"2、摊放。"},{"text":"用竹匾薄摊，环境要求清洁卫生，阴凉通风。摊放时间要适当，以叶片以软，散发青气，含水率60%左右为适度。"},{"text":"3、杀青。"},{"text":"选用600型不锈钢电热式多用机。锅底温度160～180℃，投叶量每锅1400g，其间不时通热风散发青气。用时7～8min，快速出锅。杀青程度以叶色转暗绿，叶质柔软，紧直成条，手捏成团，具有清香，含水率40%左右为宜。"},{"text":"4、做形。"},{"text":"用63型电炒锅。投叶量250g，锅温80～90℃.用双手沿锅底向同一方向理条，然后并拢，使茶叶在掌心来回搓揉。掌握轻重适度，用时6min。待条索紧直，含水率约30%时出锅摊凉。"},{"text":"5、烘干。"},{"text":"分初烘和足干，用3型热风烘干机。初烘温度控制在100～110℃，足烘温度控制在80～90℃.成茶含水率为5%～6%。"}]},{"name":"南山寿眉茶储藏方法","content":[{"text":"1、暖水瓶储藏法"},{"text":"很多人家中都有暖水瓶，暖水瓶有很好的密闭性，同时瓶内温度相对稳定，用来保存茶叶最好不过了。只需将寿眉茶茶叶装进新买的暖水瓶中，密封好即可。"},{"text":"2、木炭储藏法"},{"text":"木炭有很好的吸附湿气和异味的作用，利用木炭储藏法可以使茶叶保存更完好。在存放寿眉茶时，取适量的木炭装入小布袋内，放入存放茶叶罐的底部，然后将包装好的茶叶分层排列在罐里，再密封坛口，木炭应每个月换一次。"},{"text":"3、罐子储藏法"},{"text":"选择密封性能好的罐子进行存放茶叶也是最为常见的方式，简单又实用。其优点是贮茶方便、随饮随取。只要将从市场上买回的茶叶放入洁净的罐内即可。不过，为了更好地保持罐内干燥，可以在罐内放入1-2小包硅胶或保鲜剂，这样效果更好。"},{"text":"4、生石灰保存法"},{"text":"生石灰保存法的应用原理和木炭差不多，都是利用其作为干燥剂吸湿除味，将生石灰用布袋包装好，同时茶叶也密封包装好并远离有异味的物品，需要注意的是生石灰带最好每隔2个月换一次。"}]},{"name":"南山寿眉茶冲泡方法","content":[{"text":"步骤1、备具、水"},{"text":"准备好冲泡南山寿眉茶的\n        茶具\n        、沸水，茶杯宜选用玻璃杯，水选用纯净水或山泉水最佳。"},{"src":"\\baicha\\nanshanshoumeicha\\img_20191122101042f1vaak.jpg"},{"text":"步骤2、温杯"},{"text":"倒少许沸水到茶杯里，转旋茶杯，清洗茶杯的同时，还能起到温杯的作用。"},{"text":"步骤3、置茶"},{"text":"用茶匙量取大约3克的南山寿眉茶叶，放到茶杯里，同时观赏南寿眉的干茶，形似兰花，叶肉玉白，叶脉翠绿，鲜活欲出。"},{"text":"步骤4、润茶"},{"text":"提举水壶，让水沿杯壁注入杯中，水量约为杯子的四分之一为宜，先浸润一下南山寿眉茶叶，使其初步展开，进一步吸收水分，充分发挥香气。"},{"text":"步骤5、冲泡"},{"text":"采用回旋注水法冲泡，水量约为杯子的七分满为宜，冲泡后静置大约2分钟。"},{"text":"步骤6、品饮"},{"text":"品饮前可先闻香，观赏南山寿眉的茶汤色和杯中上下浮动的玉白透明、形似兰花的芽叶，然后小品口饮，顿感醇厚甘甜，口齿留香。"}]},{"name":"南山寿眉茶功效作用","content":[{"text":"1、寿眉性中庸，不寒不热，抗氧化，有助排出毒素及新陈代谢废物等功效，是一种很好的保健茶。"},{"text":"2、寿眉茶能保护神经细胞，对脑损伤和老年痴呆症可能有帮助;"},{"text":"3、寿眉茶能通达调节脑中神经传达物质的浓度使高血压患者降低血压;"},{"text":"4、寿眉茶有增强抗癌药物的疗效;\n        白茶\n        中含有一种特殊的成分，具有抗癌功能，饮用白茶，可以提高肝癌患者免疫能力。"},{"text":"5、减肥、护肝的作用。"},{"text":"6、南山寿眉茶的功效如同犀牛角，有清凉解毒，明目降火的奇效，可治“大火症”，在越南是小儿高烧的退烧良药。"},{"text":"7、寿眉\n        茶具\n        有消除神经紧张和镇静作用;"},{"text":"8、寿眉茶能提高学习能力与记忆力;"},{"text":"9、寿眉茶有改善女性经期综合症的作用;"},{"text":"10、寿眉有退热、祛暑之功效，为夏日佳饮。令人精神愉悦、心旷神怡，且茶中富含人体所需的氨基酸、茶多酚、矿物质、多种维生素及锌、硒等微量元素，具有生津止渴、清肝明目、提神醒脑等功效，是当今公认最安全又营养的绿色健康饮品。"}]}]}